.App {
  text-align: center;
  margin-top: 20px;
}

.game-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.players-container {
  width: 100vw;
  height: auto;
  display: flex;
  flex-direction: column;
}

h1, h2, h3 {
  margin: 0;
}

.player {
  width: 90%;
  height: auto;
  display: flex;
  flex-direction: row;
  border: 1px solid #ccc;
  padding: 10px;
  margin: 1% 5%;
  align-items: center;
  justify-content: space-evenly;
}

.player > h3 {
  padding-right: 5%;
}